<template>
  <div>
    <v-col>
      <v-row>
        <v-col>
          <h1>Shit talking by pictures</h1>
        </v-col>
      </v-row>
      <v-switch v-model="encode" :label="switchLabel"></v-switch>
      <v-container v-if="encode">
        <v-textarea
          outlined
          name="input"
          label="Input"
          v-model="input"
          class="text-area"
        >
        </v-textarea>
        <v-container v-if="imagePath">
          <img
            height="500"
            width="500"
            :src="imagePath"
            :lazy-src="imagePath"
            id="image-output"
          />
        </v-container>
        <v-row>
          <v-btn @click="encrypt">Encrypt</v-btn>
        </v-row>
      </v-container>
      <v-container v-if="!encode">
        <v-file-input
          accept="image/png"
          placeholder="Select Your Input"
          prepend-icon="mdi-camera"
          label="Input"
          v-model="imageInput"
          @change="showUploadedImage"
        ></v-file-input>
        <v-container v-if="imageInputPath">
          <img
            height="500"
            width="500"
            :src="imageInputPath"
            :lazy-src="imageInputPath"
            id="image-input"
          />
        </v-container>
        <v-textarea
          outlined
          name="output"
          label="Output"
          v-model="output"
          class="text-area"
        >
        </v-textarea>
        <v-row>
          <v-btn @click="decrypt">Decrypt</v-btn>
        </v-row>
      </v-container>
    </v-col>
  </div>
</template>

<script>
export default {
  name: "Converter",
  data() {
    return {
      encode: false,
      input: "Enter your text here",
      output: "Your output will appear here",
      imagePath: "",
      imageInput: null,
      imageInputPath: "",
    };
  },
  computed: {
    switchLabel() {
      if (this.encode) {
        return "Encrypt";
      } else {
        return "Decrypt";
      }
    },
  },
  methods: {
    encrypt() {
      //var base64 = require('base-64')
      var charCodes = this.stringToCharCodes(this.input);
      var height = Math.ceil(Math.sqrt(charCodes.length));
      var width = height;
      var buffer = this.charCodesToBuffer(charCodes, height, width);
      console.log('Input: ' +buffer);

      var canvas = document.createElement("canvas"),
        ctx = canvas.getContext("2d");

      canvas.width = width;
      canvas.height = height;

      // create imageData object
      var idata = ctx.createImageData(width, height);

      // set our buffer as source
      idata.data.set(buffer);

      // update canvas with new data
      ctx.putImageData(idata, 0, 0);

      var dataUri = canvas.toDataURL();
      // optional callback function

      var image = new Image();
      image.src = dataUri;
      this.imagePath = dataUri;
      this.image = image;
      //document.body.appendChild(image);
      //var vImg = document.getElementById('image-output').set(image)
    },
    async decrypt() {
      var image = new Image();
      image.src = this.imageInputPath;

      var promiseArray = [];
      promiseArray.push(
        new Promise((resolve) => {
          image.onload = function () {
            resolve();
          };
        })
      );
      await Promise.all(promiseArray);
      var canvas = document.createElement("canvas"),
        ctx = canvas.getContext("2d");
      ctx.drawImage(image, 0, 0);

      var buffer = ctx.getImageData(0, 0, image.height, image.width).data;

      console.log('Output: ' + buffer);
      var charCodeArray = [];
      for (var i = 0; i < buffer.length; i += 4) {
        var hexCharCode = "";
        hexCharCode += Math.round((255-buffer[i + 3]) / 16).toString("16");
        hexCharCode += Math.round(buffer[i] / 16).toString("16");
        hexCharCode += Math.round(buffer[i + 1] / 16).toString("16");
        hexCharCode += Math.round(buffer[i + 2] / 16).toString("16");

        var charCode = parseInt(hexCharCode, 16);
        if (charCode != 0) {
          charCodeArray[charCodeArray.length] = charCode;
        }
      }
      this.output = String.fromCharCode.apply(null, charCodeArray);
    },
    stringToCharCodes(str) {
      return str.split("").map((char) => {
        return char.charCodeAt(0).toString(16);
      });
    },
    charCodesToBuffer(codes, height, width) {
      var buffer = new Uint8ClampedArray(width * height * 4);
      var length = width * height;
      for (var i = 0; i < length; i++) {
        if (codes[i]) {
          buffer[i * 4] = this.getRedField(codes[i]);
          buffer[i * 4 + 1] = this.getGreenField(codes[i]);
          buffer[i * 4 + 2] = this.getBlueField(codes[i]);
          buffer[i * 4 + 3] = 255 - this.getAlphaField(codes[i]);
        } else {
          buffer[i * 4] = 0;
          buffer[i * 4 + 1] = 0;
          buffer[i * 4 + 2] = 0;
          buffer[i * 4 + 3] = 255;
        }
      }

      return buffer;
    },
    getAlphaField(code) {
      if (code.length < 4) {
        return 0;
      }
      var value = parseInt(code[code.length - 4], 16);
      return value * 16;
    },
    getRedField(code) {
      if (code.length < 3) {
        return 0;
      }
      var value = parseInt(code[code.length - 3], 16);
      return value * 16;
    },
    getGreenField(code) {
      if (code.length < 2) {
        return 0;
      }
      var value = parseInt(code[code.length - 2], 16);
      return value * 16;
    },
    getBlueField(code) {
      if (code.length < 1) {
        return 0;
      }
      var value = parseInt(code[code.length - 1], 16);
      return value * 16;
    },
    showUploadedImage() {
      this.imageInputPath = URL.createObjectURL(this.imageInput);
    },
    roundToNearestFactor(num){
      return Math.round(num/16) * 16;
    }
  },
};
</script>

<style scoped>
.text-area {
  padding: 3rem;
}
</style>
